.wrapper {
    background: rgb(36, 36, 36);
}

.label {
    color: #dddddd;
    margin: 0 10px;
    /*animation-name: example;*/
    animation-duration: 4s;
    animation-iteration-count: infinite;
}

input[type="search"] {
    background: rgba(36, 36, 36, .2);
    padding: 1px 1px 1px 5px;
    position: relative;
    top: 0;
    left: 0;
    width: 178px;
    height: 25px;
    outline: none;
    border: 1px solid #CCCCCC;
    border-radius: 6px;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    transition: all .5s;

}

input[type="search"]:focus {
    background: #FFF;
    padding: 1px 1px 1px 5px;
    width: 300px;
    height: 25px;
    border: 1px solid #CCCCCC;
    top: 0;
    right: 100%;

}

@keyframes example {
    0% {
        color: red;
    }
    25% {
        color: yellow;
    }
    50% {
        color: blue;
    }
    75% {
        color: green;
    }
    100% {
        color: red;
    }
}

@media only screen and (max-width: 375px) {
    .wrapper {
        /*margin: 0 20px;*/
    }

    .input {
        width: 90vw;
        margin: 0 20px;
    }
}